import React from 'react'
import galleryImages from './galleryImages'
import Masonry, {ReactiveMasonry, ResponsiveMasonry} from 'react-responsive-masonry'

const MasonryImgGallery = () => {
  return (
    <ResponsiveMasonry columnsCountBreakPoints={{350:1 ,768:3, 992:4}}>
        <Masonry gutter = '1rem'>{
            galleryImages.map((item,index)=>(
                <img 
                className = "masonry__img"
                src={item } key={index} alt="" style={
                    {'width' : '100%','display':'block', 'borderRadius':'10px' }
                    
                } />
            ))
        }

        </Masonry>

    </ResponsiveMasonry>
  )
}

export default MasonryImgGallery